import './Carousel.sass';

import React, { Component } from 'react';
import { object, array } from 'prop-types';

import MultiCarousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';

import CarouselItem from '../CarouselItem/CarouselItem';
import CarouselControl from '../CarouselControl/CarouselControl';

/**
 * @class Carousel
 * @summary  
 */
class Carousel extends Component {
	//    
	static propTypes = {
		list: array.isRequired,
		settings: object.isRequired,
	};

	//   
	static defaultProps = {
		customCarouselItem: null,
	};

	//  
	multiCarousel = null;

	//  
	state = {
		isMoving: false,
	};

	//   
	componentDidMount() {
		window.addEventListener('resize', () => {
			this.multiCarousel && this.multiCarousel.goToSlide(0, true);
		});
	}

	// 
	render() {
		const { list, settings, customCarouselItem, containerClass } = this.props;
		const { isMoving } = this.state;
		const Item = customCarouselItem || CarouselItem;

		return (
			<MultiCarousel
				ref={(el) => (this.multiCarousel = el)}
				beforeChange={() => this.setState({ isMoving: true })}
				afterChange={() => this.setState({ isMoving: false })}
				//  : http://bit.ly/2Maqjm3
				//  
				responsive={settings}
				//   
				containerClass={`carousel ${containerClass || ''}`.trim()}
				sliderClass="carousel__slider"
				itemClass="carousel__slide"
				draggable={true}
				//      
				focusOnSelect={false}
				//      
				partialVisible={true}
				//      
				minimumTouchDrag={40}
				//    
				customLeftArrow={
					<CarouselControl label=" " direction="left" style={{ left: 4 }} />
				}
				customRightArrow={
					<CarouselControl label=" " style={{ right: 4 }} />
				}
			>
				{list.map((item, index) => (
					<Item
						key={item.id}
						nth={index + 1}
						total={list.length}
						item={item}
						isMoving={isMoving}
					/>
				))}
			</MultiCarousel>
		);
	}
}

export default Carousel;
